
import SearchBox from '@/base/search-box/search-box'
import Suggest from '@/components/Suggest/Suggest'
import SearchList from '@/base/search-list/search-list'
import scroll from '@/base/scroll/scroll'
import { playlistMixin, search } from '@/common/js/mixin'
import Confirm from 'vux/src/components/confirm/index.vue'

import { getHotKey } from '@/common/api/hotKey'
import { trim } from '@/common/js/utils'
import { mapActions, mapGetters } from 'vuex'

export default {
mixins: [playlistMixin, search],
data () {
return {
hotKey: [],
confirmShow: false
}
},
computed: {
...mapGetters(['searchHistory'])
},
methods: {
...mapActions([
'deleteFromHistory',
'emptySearchHistory'
]),
_getHotKey () {
getHotKey().then(res => {
this.hotKey = res.data.hotkey.slice(0, 10)
})
},
_selectHotKey (query) {
query = trim(query)
this.$refs['search'].setQuery(query)
},
deleteOne (query) {
this.deleteFromHistory(query)
},
showConfirm () {
this.confirmShow = true
},
onConfirm () {
this.emptySearchHistory()
},
onCancel () {
this.confirmShow = false
},
pickHistory (query) {
this.$refs['search'].setQuery(query)
},
handlePlaylist () {
if (this.searchHistory.length) {
let bottom
this.playlist.length ? (bottom = '60px') : (bottom = '')
setTimeout(() => {
this.$refs['fix-hook'].style['padding-bottom'] = bottom
this.$refs['scroll'].refresh()
}, 500)
}
},
fresh () {
if (this.searchHistory.length) {
this.$refs['scroll'].refresh()
}
}
},
components: {
SearchBox,
Suggest,
SearchList,
Confirm,
scroll
},
created () {
this._getHotKey()
},
mounted () {
if (this.searchHistory.length) {
setTimeout(() => {
this.$refs['scroll-warpper'].style.height = `${document.documentElement
.clientHeight - 296}px`
}, 1000)
}
}
}



